<script type="text/ecmascript-6">
import { loginAction } from './loginAction';
export default {
  setup() {
    return {
      ...loginAction()
    };
  },

  render() {
    return (
      <div class='my-demo-login'>
        <div class='container'>
          <h1 >
            <img
              src={ this.loginLogo.value }
            />
          </h1>
          <label class='container-tip'></label>
          <form
            onSubmit={ (e) => this.formSubmit(e) }
            class='login-form-validator'
          >
            <div class='form-control' title=''>
              <input
                onInput={(e) => this.inputChange(e, 'account')}
                onBlur={ e => this.formInvalid(e, 'account') }
                type='text'
                required
                autofocus
              />
              <label></label>
            </div>

            <div class='form-control' title=''>
              <input
                onInput={(e) => this.inputChange(e, 'password')}
                type='password'
                required
              />
              <label></label>
            </div>

            <button
              class='btn ripple'
              onClick={ () => this.goLogin() }
            >
              
            </button>

            <p class='text' />
          </form>
        </div>
      </div>
    );
  }
};
</script>

